*{
  padding: 0;
  margin: 0;
  transition: 0.6s;
}
.backImage{
  width: 100%;
  position: fixed;
  height: 100vh;
  z-index: -100;
}
.backImage img{
  height: 100vh;
  width: 100%;
  object-fit: cover;
}
.backImageDark{
  width: 100%;
  height: 100vh;
  background: rgba(255, 255, 255, 0.568);
  z-index: -99;
  position: fixed;
}
a{
  text-decoration: none;
  color: #fb8500;
}
a:hover{
  text-decoration: underline;
  color: #ff1100;
  transform: scale(1.02);
}
.headText{
  text-align: center;
  font-size: 20px;
  margin: 20px;
  color: rgba(73, 0, 0, 0.521);
}
.main_box{
  max-width: 600px;
  width: 90%;
  margin: auto;
  min-height: 90vh;
  padding: 20px;
  background: #fff;
  border-radius: 10px;
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.418);
}
.form-floating{
  max-width: 500px;
  margin: auto;
}
.palaga{
  color: rgb(88, 88, 88);
}
.messageArea{
  border: 1px solid rgba(0, 0, 0, 0.171);
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.212);
  background: rgb(244, 253, 255);
}
.messageArea:focus{
  border: 1px solid rgba(0, 0, 0, 0.171);
  background: rgb(255, 255, 255);
  box-shadow: 1px 1px 3px rgba(116, 176, 255, 0.781);
}
.btn{
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.418);
}
.schoolName{
  width: fit-content;
  margin: auto;
  display: flex;
}
.schoolName img{
  width: 70px;
}
.LOgName{
  padding: 20px;
  font-size: 20px;
  line-height: 20px;
  font-weight: 600;
  height: 70px;
  color: rgba(0, 0, 0, 0.521);
}
.paraBox{
  padding-top: 20px;
}
.paragraph{
  color: rgba(0, 0, 0, 0.404);
  text-align: center;
}
.SeePost{
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  width: fit-content;
  margin: auto;
  border: 2px solid #fb8500;
  padding: 10px;

}
.SeePost:hover {
  border: 2px solid #ff1100;
  color: #ff1100;
  transform: scale(1.08);
  background: #ffe2e0;
}
::-moz-selection { /* Code for Firefox */
  background: rgba(0, 255, 157, 0.288);
}

::selection {
  background: rgba(0, 255, 157, 0.288);
}
.txt_right small{
  width: 100%;
  text-align: right;
}
.text_blc{
  color: #000;
}
.Headdtxt{
  width: fit-content;
  margin: auto;
  border-bottom: 3px solid #fb8500;
}
.Headdtxt h5{
  text-align: center;
}
.ImgCarder{
  padding: 10px;
}
.ImgCarder p{
margin: auto;
padding: 10px 0;
font-size: larger;
font-weight: 500;
text-align: center;
}
.ImgCarder{
  margin-bottom: 30px;
  }
  .colorChange{
    color: rgb(173, 173, 173);
    border: 2px solid rgb(206, 206, 206);
    padding: 10px;
    border-radius: 10px;
    margin: 10px;
  }
  .SucessMessage{
    background: #03ff03;
    text-align: center;
    padding: 5px;
  }
  .ErrorMessage{
    background: #ff3e03;
    text-align: center;
    padding: 5px;
  }
  #VisibleSucess_Off{
    display: none;
    transition: ease-in;
    transition: 1s;
  }
  #VisibleSucess{
    display: block;
    transition: 1s;
    transition: ease-in;
  }
  